<template>
  <div class="xqlist">
    <!-- 头部 -->
        <div class="msg_heard">
            <div @click="back">
                <van-icon name="arrow-left" color="#fff"  />
            </div>
            <div><p class="p">列表详情</p></div>
            <div></div>
        </div>
        <!-- 标题 -->
        <div class="content_title"><h2>{{arr.title}}</h2></div>
        <!-- 作者 -->
        <div class="content_author">
          <div class="content_left">
              <div>
                <van-image
                  round
                  width="40px"
                  height="40px"
                  :src='arr.avatar'
                />
              </div>
              <!--  https://img01.yzcdn.cn/vant/cat.jpeg-->
              <div class="content_time">
                <p>作者:{{arr.author}}</p>
                <p class="time">{{new Date(arr.time).toLocaleDateString() }}</p>
              </div>
          </div>
          <div>
            <van-button icon="plus" round  type="primary" class="btn_color" @click="isshow">{{show1?'关注':'已关注'}}</van-button>
          </div>
        </div>
        <!-- 内容 -->
        <div class="neirong">
          <p>{{arr.content}}</p>
          <div v-for="(item,i) in arr.imageSrc" :key="i">
            <img :src="item" alt="" srcset="" @error="imgError" />
          </div>
        </div>
        <!-- 评论列表 -->
        <div class="content_author1" v-for="(item,i) in pinglunlist" :key="i">
          <div class="content_left">
              <div>
                <van-image
                  round
                  width="40px"
                  height="40px"
                  :src='arr.avatar'
                />
              </div>
              <!--  https://img01.yzcdn.cn/vant/cat.jpeg-->
              <div class="content_time">
                <p>作者:{{item.info.username}}</p>
                <h6>{{item.content}}</h6>
                <div class="content_btn">
                  <p class="time">{{new Date(item.create_time).toLocaleDateString() }}</p>
                  <button  class="huifu"> {{item.reply_num}}回复</button>
                </div>
              </div>
          </div>
          <div>
            <van-icon name="good-job" />
            <span>{{item.like_count}}</span>
          </div>
        </div>
        <!-- 底部 -->
        <div class="xq_footer">
          <div class="pinglun">
            <van-cell >
                  <van-button round type="info" class="btn" @click="showPopup">写评论</van-button>
            </van-cell>
            <van-popup v-model="show"  closeable position="bottom"  :style="{ height: '40%' }">
              评论
              <van-field
                v-model="message"
                autosize
                autofocus
                type="textarea"
                maxlength="1500"
                placeholder="请输入评论"
                show-word-limit
                class="text"
              />
              <div class="baocun">
                <van-button type="primary"  block>发布</van-button>
              </div>
            </van-popup>
            
          </div>
          <van-icon name="comment-o" size="20"  />
          <van-icon name="star" size="20"  />
          <van-icon name="good-job" size="20"  />
          <van-icon name="share" size="20" />
        </div>
        <div>
         
        </div>
  </div>
</template>

<script>
import { Cell, CellGroup ,Icon,Button,Popup,Image as VanImage,Field } from 'vant';
import axios from 'axios'
export default {
  data() {
    return {
        arr:[],
        show1:true,
        show:false,
        value: '',
        message:'',
        pinglunlist:[]
    }
  },
  components:{
    [Cell.name]:Cell,
    [CellGroup.name]:CellGroup,
    [Icon.name]:Icon,
    [Button.name]:Button,
    [VanImage.name]:VanImage,
    [Popup.name]:Popup ,
    [Field.name]:Field 
   },
  methods:{
    back(){
        this.$router.go(-1);
    },
    isshow(){
      this.show1=!this.show1
      
    },
    showPopup() {
      this.show = true;
    },
   },
  created(){
    // console.log(this.$route.params.id);
    axios({
            method:'post',
            url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_article_detail',
            data:{
                    "article_id":this.$route.params.id,
                }    
            }).then(res=>{
                this.arr=res.data.data
                // console.log(this.arr);
      }),
      axios({
            method:'post',
            url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/api/get_comment_list',
            data:{
                    "article_id":this.$route.params.id,
                    "skip":0,
                    "limit":5
                }    
            }).then(res=>{
                this.pinglunlist=res.data.data
                // console.log('1',res);
      })
  }
}
</script>

<style lang="less" scoped>
.xqlist{
  margin-bottom: 52px;
  margin-top: 48px;
}
.msg_heard{
    display: flex;
    height: 45px;
    background-color: rgb(16, 173, 81);
    // line-height: 45px;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999;
    .p{
        color: #fff;
        text-align: center;
    }
}
.content_title{
  margin: 20px 10px;
  h2{width: 360px; word-break: break-all;  //长单词强制换行
    }
}
.content_author{
  display: flex;
  justify-content: space-between;
  padding: 10px 10px;
  align-items: center;
  border-bottom: 1px solid rgb(212, 222, 222);
  .content_left{
    display: flex;
    font-size: 8px;
    align-items:stretch;
    .content_time{
      margin-left: 10px;
      .time{
      margin-top: 10px;
    }
    }
    
  }
}
.btn_color{
  height: 20px;
  color: #000;
  background-color: #fff;
  padding: 15px;
}
.neirong{
  // width: 360px;
  padding: 10px;
  p{
    width: 360px; 
    // border: 1px solid;
    word-break: break-all;  //长单词强制换行
  }

  img{
    margin-top: 10px;
    width: 100%;
    height: 100%;
  }
}
.content_author1{
  display: flex;
  justify-content: space-between;
  padding: 10px 10px;
  align-items: stretch;
  border-bottom: 1px solid rgb(212, 222, 222);
  .content_left{
    display: flex;
    font-size: 8px;
    align-items:stretch;
    .content_time{
      margin-left: 10px;
      h6{
        width: 250px;
        // border: 1px solid;
      }
      .content_btn{
         display: flex;
         align-items: center;
         .huifu{
          margin-left: 10px;
          width: 50px;
          height: 20px;
          font-size: 2px;
          background-color: #fff;
          color: #000;
          border: 1px solid #ccc;
          border-radius: 10px;
         }
      }
      .time{
        margin-top: 5px;
    }
    }
    
  }
}

.xq_footer{
  background: #fff;
  height: 50px;
  border-top: 1px solid rgb(193, 188, 188);
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-left: 10px;
  position: fixed;
  bottom:0;
  right: 0;
  width: 98%;
  .van-cell{
    padding: 0;
  }
  .btn{
    width: 100%;
    height: 30px;
    background-color: #fff;
    color: #000;
    border: 1px solid ;
  }
  .pinglun{
    height: 30px;
    flex: 3;
    // background-color: #fff;
    // color: #000;
    // border: 1px solid ;
    .text{
      margin-top: 10px;
      padding: 10px;
    }
    .baocun{
      width: 350px;
      margin: 25px auto;
    }
  }
  .van-icon{
    margin: 0 10px;
  }
}
</style>